html {
  font-family: Arial, Helvetica, sans-serif;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
}

.block-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.block {
  display: flex;
  height: 300px;
  flex-basis: 100%;
  font-family: inherit;
  font-size: 100px;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: #fff;
}

.block--1 {
  background-color: rgb(255, 0, 0);
}

.block--2 {
  background-color: rgb(204, 0, 0);
}

.block--3 {
  background-color: rgb(153, 0, 0);
}

.block--4 {
  background-color: rgb(102, 0, 0);
}

.block--5 {
  background-color: rgb(51, 0, 0);
}

.block--6 {
  background-color: rgb(0, 0, 0);
}

@media (min-width: 600px) {
  .block {
    flex-basis: 50%;
  }
  .block--1 {
    order: 0;
  }

  .block--2 {
    order: 1;
  }

  .block--3 {
    order: 3;
  }

  .block--4 {
    order: 2;
  }

  .block--5 {
    order: 5;
  }

  .block--6 {
    order: 6;
  }
}

@media (min-width:900px) {
  .block {
    flex-basis: 33.33%;
  }

  .block--1 {
    order: 0;
  }

  .block--2 {
    order: 1;
  }

  .block--3 {
    order: 2;
  }

  .block--4 {
    order: 6;
  }

  .block--5 {
    order: 5;
  }

  .block--6 {
    order: 4;
  }
}
